.image-hero{
  margin-bottom: 4em;
  img{
    border-radius: 1em;
    box-shadow: 0 0 2px #333,
                0 0 10px rgba(#111,.3);
    margin-bottom: 2em;
  }
  h1{
    font-family: var(--body-font);
    text-transform: initial;
    padding: 0 5vw;
  }
}

.tramites{
  --tramite-height: 4rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.7em;
  margin-bottom: 5em;
  &.usa{
    display: block
  }
  .mobile & {
    display: block;
  }
}

.tramite{
  .mobile & {
    margin-bottom: 1.3rem;
  }
  &__list,
  &__sublist {
    @extend %list-style;
  }
  // &__sublist li{
  //   background-image: url(http://www.tramitamexusa.com/wp-content/uploads/2021/04/button.png);

  // }
  &__list{
    padding-left: 2.2rem;
    padding-right: 1rem;
    padding-top: 0;
    padding-bottom: 0;
    max-height: 0;
    overflow: hidden;
    transition: all .5s;
    &.show{
      max-height: 800px;
      padding-top: 1rem;
      padding-top: 1rem;
      padding-bottom: 1rem;
    }
    li{
      position: relative;
      margin-bottom: .5em;
      &:last-child {
        margin-bottom: 0;
      }
      &::before{
        content: '\65';
        display: block;
        width: 1em;
        height: 1em;
        font-family: tramita;
        position: absolute;
        left: -1.2em;
        top: 1px;
      }
      &.sl{
        // font-weight: 700 !important;
        cursor: pointer;
        & .hover::after{
          content: '';
          display: block;
          width: 100%;
          height: 1px;
          background-color: $font-color;
          position: absolute;
          bottom: 0;
          opacity: .5;
        }
      }
      > span{
        position: relative;
        top: 2px;
        margin-left: 1em;
        text-decoration: none !important;
        &.hover{
          position: relative;
          margin-left: 0;
          display: inline-block;
          top: 0;
          &:hover{
            color: #111;
          }
        }
      }
    }
  }
  &__sublist{
    margin-top: .5em;
    padding-left: 1em;
    transition: all .5s;
    max-height: 0;
    overflow: hidden;
    &.show{
      max-height: 200px;
    }
    li{
      cursor: default;
      margin-bottom: .3em;
      &:last-child{
        margin-bottom: 0;
      }
      &::before{
        content: '\69';
        opacity: .75;
        top: 2px;
      }
    }
  }
  &__name{
    background-color: var(--color-main);
    font-size: 1.2em !important;
    color: #fdfdfd;
    height: var(--tramite-height);
    // display: grid;
    // grid-template-columns: 15% 1fr 15%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: .3rem;
  }
  &__img{
    height: 100%;
    max-height: var(--tramite-height);
    padding: .2rem;
    display: flex;
    align-items: center;
    width: 10%;
    margin-right: 1em;
  }
  i{
    display: grid;
    justify-content: center;
    margin-left: 1em;
  }
  p{
    text-align: center;
    font-weight: 700 !important;
    line-height: 1.1;
  }
  svg{
    fill: #fdfdfd;
  }
  // h2{
  //   height: 100%;
