.main-header{
  --top-bar-height: 7px;
  height: var(--header-height);
  position: relative;
  box-shadow: 0 0 2px rgba(#000, .3);
  margin-bottom: 4em;
  
  .item-email{
    display: none;
  }
  & > .container {
    position: relative;
    top: var(--top-bar-height);
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 1%;
    align-self: center;
    height: calc(var(--header-height) - var(--top-bar-height));
    .mobile & {
      grid-template-columns: 1fr auto;
    }
  }
  &::after{
    content: "";
    position: absolute;
    width: 100%;
    height: var(--top-bar-height);
    background-color: var(--color-main);
    top: 0;
    left: 0;
  }
}
.social-network{
  $size: 1.4em;
  @extend %list-style;
  display: flex;
  height: calc(var(--header-height) - var(--top-bar-height));
  align-items: center;
  .mobile{
    display: none;
  }
  li, a {
    display: block;
    height: $size;
    width: $size;
  }
  li{
    margin-right: .5em;
    &:last-child{
      margin-right: 0;
    }
  }
  a{
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover{
      svg{
        fill: --color-main !important;
      }
    }
  }
  svg{
    max-width: 100%;
    height: auto;
    max-height: 100%;
    fill: $font-color;
    transition: all .3s;
  }
}
.Logo{
  max-width: 100%;
  height: calc(var(--header-height) - var(--top-bar-height));
  display: flex;
  align-items: center;
  img{
    height: calc((var(--header-height) - var(--top-bar-height)) * .6);
    width: auto;
  }
}