.mobile .contacto__title {
  text-align: center;
  margin-bottom: 2.3rem;
  width: 100%;
}

.contacto__title::after {
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: var(--color-main);
  bottom: -1rem;
}

.contacto__content {
  background: url(https://raw.githubusercontent.com/yagoestevez/fcc-portfolio/master/src/Images/envelope.svg?sanitize=true) no-repeat right, #181d23;
  background-size: 40%;
  background-position: 90%;
  padding-top: 2em;
  color: #fff;
  padding-bottom: 1.1em;
}

@media screen and (min-width: 1024px) {
  .contacto__content {
    clip-path: polygon(0 0, 20% 100px, 100% 0, 100% 100%, 0 100%);
    padding-top: 10%;
  }
}

.contacto__content .section-title {
  text-shadow: texto3d(2, black);
}

.contacto__header {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 1em;
  display: grid;
  grid-template-columns: 50%;
  grid-gap: 2em;
}

.contacto__container {
  max-width: 600px;
  margin: 0 auto;
  padding: 0 1em;
  grid-gap: 2em;
}

.contacto__container > .msg {
  font-size: 1.2em;
  text-align: center;
}

.contacto__container .form {
  margin-top: 1.5em;
}

.contacto__container .input-group {
  margin-bottom: 1.4em;
}

.contacto__container .input-group:last-child, .contacto__container .input-group:last-of-type {
  margin-bottom: 0;
}

.contacto__container .input-group input, .contacto__container .input-group textarea {
  width: 100%;
  font-family: inherit;
  line-height: 2em;
  font-size: 1.05em;
  border: none;
  border-bottom: 2px solid rgba(253, 253, 253, 0.5);
  border-radius: 0.3rem;
  background-color: rgba(255, 255, 255, 0.025);
  padding: 0 .5em;
  color: rgba(253, 253, 253, 0.9) !important;
}

.contacto__container .input-group input::placeholder, .contacto__container .input-group textarea::placeholder {
  color: rgba(253, 253, 253, 0.9) !important;
}

.contacto__container .input-group input:focus, .contacto__container .input-group textarea:focus {
  box-shadow: none !important;
  outline: none !important;
}

.contacto__container textarea {
  resize: none;
  height: 6em;
  line-height: 1;
}

.contact__header {
  max-width: 600px;
  padding: 0 1em;
  margin: 0 auto 2em;
  display: grid;
  grid-template-columns: 1fr 60%;
  grid-gap: 2em;
  justify-content: center;
  align-items: start;
}

.mobile .contact__header {
  display: block;
}

.contact__header .section-title {
  text-align: left;
  margin: 0;
  padding: 0;
  text-shadow: none;
}

@media screen and (max-width: 639px) {
  .contact__header .section-title {
    font-size: 1.1em;
  }
}

.contact__header .section-title::after {
  left: 0;
  bottom: -0.3em;
  transform: none;
}

@media screen and (max-width: 639px) {
  .contact__header .section-title::after {
    display: none;
  }
}

.contact__header .sn-menu {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1em;
}

.mobile .contact__header .sn-menu {
  justify-content: center;
}

.contact__header .sn-menu svg {
  width: auto;
  height: 100%;
  fill: #fdfdfd;
}

.contact__header .sn-menu li, .contact__header .sn-menu a, .contact__header .sn-menu svg {
  display: block;
}

.contact__header .sn-menu li, .contact__header .sn-menu a {
  width: 3em;
  height: 1.5em;
}

.contact__header .sn-menu li {
  margin-right: .5em;
}

.contact__header .sn-menu li:last-child {
  margin-right: 0;
}

.contact__header .sn-menu a {
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact__header .address {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-size: 1.2em;
}

.mobile .contact__header .address {
  align-items: center;
}

.contact__header p {
  text-align: right;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: rgba(253, 253, 253, 0.9);
  box-shadow: 0 0 0px 1000px none inset;
  transition: background-color 5000s ease-in-out 0s;
}

.footer-form {
  display: grid;
  grid-template-columns: 1fr auto;
  margin-top: 1.3em;
}

.footer-form button {
  line-height: 2em;
  border: none;
  background: #1D6FB8;
  color: #fdfdfd;
  padding: 0 1.3em;
  font-size: 1.05em;
  border-radius: .3rem;
  transition: background .3s;
  cursor: pointer;
}

.footer-form button:hover {
  background: #16548c;
}

.footer-form .note {
  text-align: center;
  height: 2.3em;
}

.footer-form .note.success {
  color: #4db6ac;
}

.footer-form .note.error {
  color: #ef5350;
}

.image-hero {
  margin-bottom: 4em;
}

.image-hero img {
  border-radius: 1em;
  box-shadow: 0 0 2px #333, 0 0 10px rgba(17, 17, 17, 0.3);
  margin-bottom: 2em;
}

.image-hero h1 {
  font-family: var(--body-font);
  text-transform: initial;
  padding: 0 5vw;
}

.tramites {
  --tramite-height: 4rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.7em;
  margin-bottom: 5em;
}

.tramites.usa {
  display: block;
}

.mobile .tramites {
  display: block;
}

.mobile .tramite {
  margin-bottom: 1.3rem;
}

.tramite__list {
  padding-left: 2.2rem;
  padding-right: 1rem;
  padding-top: 0;
  padding-bottom: 0;
  max-height: 0;
  overflow: hidden;
  transition: all .5s;
}

.tramite__list.show {
  max-height: 800px;
  padding-top: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.tramite__list li {
  position: relative;
  margin-bottom: .5em;
}

.tramite__list li:last-child {
  margin-bottom: 0;
}

.tramite__list li::before {
  content: '\65';
  display: block;
  width: 1em;
  height: 1em;
  font-family: tramita;
  position: absolute;
  left: -1.2em;
  top: 1px;
}

.tramite__list li.sl {
  cursor: pointer;
}

.tramite__list li.sl .hover::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background-color: #222;
  position: absolute;
  bottom: 0;
  opacity: .5;
}

.tramite__list li > span {
  position: relative;
  top: 2px;
  margin-left: 1em;
  text-decoration: none !important;
}

.tramite__list li > span.hover {
  position: relative;
  margin-left: 0;
  display: inline-block;
  top: 0;
}

.tramite__list li > span.hover:hover {
  color: #111;
}

.tramite__sublist {
  margin-top: .5em;
  padding-left: 1em;
  transition: all .5s;
  max-height: 0;
  overflow: hidden;
}

.tramite__sublist.show {
  max-height: 200px;
}

.tramite__sublist li {
  cursor: default;
  margin-bottom: .3em;
}

.tramite__sublist li:last-child {
  margin-bottom: 0;
}

.tramite__sublist li::before {
  content: '\69';
  opacity: .75;
  top: 2px;
}

.tramite__name {
  background-color: var(--color-main);
  font-size: 1.2em !important;
  color: #fdfdfd;
  height: var(--tramite-height);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  border-radius: .3rem;
}

.tramite__img {
  height: 100%;
  max-height: var(--tramite-height);
  padding: .2rem;
  display: flex;
  align-items: center;
  width: 10%;
  margin-right: 1em;
}

.tramite i {
  display: grid;
  justify-content: center;
  margin-left: 1em;
}

.tramite p {
  text-align: center;
  font-weight: 700 !important;
  line-height: 1.1;
}

.tramite svg {
  fill: #fdfdfd;
}
