.contacto {
  //min-height: 100vh;
  color: #fff;

  &__title{
    text-align: left;
    position: relative;
    display: table;
    .mobile & {
      text-align: center;
      margin-bottom: 2.3rem;
      width: 100%;
    }
    &::after{
      content: '';
      display: block;
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: var(--color-main);
      bottom: -1rem;
    }
  }

  &__content {
    background: url(https://raw.githubusercontent.com/yagoestevez/fcc-portfolio/master/src/Images/envelope.svg?sanitize=true) no-repeat right,
    #181d23;
    background-size: 40%;
    background-position: 90%;
    padding-top: 2em;
    color: #fff;
    padding-bottom: 1.1em;

    @include from(lg) {
      clip-path: polygon(0 0, 20% 100px, 100% 0, 100% 100%, 0 100%);
      padding-top: 10%;
    }

    .section-title {
      text-shadow: texto3d(2, rgba(#000, 25));
    }
  }
  &__header{
    max-width: 600px;
    margin: 0 auto;
    padding: 0 1em;
    display: grid;
    grid-template-columns: 50%;
    grid-gap: 2em;
  }

  &__container {
    max-width: 600px;
    margin: 0 auto;
    padding: 0 1em;
    grid-gap: 2em;
    & > .msg{
      font-size: 1.2em;
      text-align: center;
      // margin-bottom: 1.5em;
      // padding-bottom: 1.5em;
    }
    .form{
      margin-top: 1.5em;
    }
    .input-group{
      margin-bottom: 1.4em;
      &:last-child, &:last-of-type{
        margin-bottom: 0;
      }
      input, textarea{
        width: 100%;
        font-family: inherit;
        line-height: 2em;
        font-size: 1.05em;
        border: none;
        border-bottom: 2px solid rgba(#fdfdfd, .5);
        border-radius: 0.3rem;
        background-color: rgba(#fff,.025);
        padding: 0 .5em;
        color: rgba(#fdfdfd, .9) !important;
        &::placeholder {
          color: rgba(#fdfdfd, .9) !important;
          // font-weight: 700;
        }
        &:focus{
          box-shadow: none !important;
          outline: none !important;
        }
      }
    }
    textarea{
      resize: none;
      height: 6em;
      line-height: 1;
    }
  }
}
.contact__header {
  //background: #006dc6;
  max-width: 600px;
  padding: 0 1em;
  margin: 0 auto 2em;
  display: grid;
  grid-template-columns: 1fr 60%;
  grid-gap: 2em;
  justify-content: center;
  align-items: start;
  .mobile & {
    display: block;
  }

  .section-title {
    text-align: left;
    margin: 0;
    padding: 0;
    text-shadow: none;
    

    @include to(m) {
      font-size: 1.1em;
      
    }

    &::after {
      left: 0;
      bottom: -0.3em;
      transform: none;

      @include to(m) {
        display: none;
      }
    }
  }

  .sn-menu{
    @extend %list-style;
    $size: 1.5em;
    display: flex;
    justify-content: flex-end;
    margin-bottom: 1em;
    .mobile & {
      justify-content: center;
    }
    svg{
      width: auto;
      height: 100%;
      fill: #fdfdfd;
    }
    li, a,svg{
      display: block;
    }
    li,a{
      width: $size * 2;
      height: $size;
    }
    li{
      margin-right: .5em;
      &:last-child{
        margin-right: 0;
      }
    }
    a{
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  .address{
    @extend %list-style;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-size: 1.2em;
    .mobile & {
      align-items: center;
    }
  }
  p {
    text-align: right;
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  //border: 1px solid green;
  -webkit-text-fill-color: rgba(#fdfdfd, .9);
  box-shadow: 0 0 0px 1000px none inset;
  transition: background-color 5000s ease-in-out 0s;
}
.footer-form{
  display: grid;
  grid-template-columns: 1fr auto;
  margin-top: 1.3em;
  button{
    line-height: 2em;
    border: none;
    background: $color-fisrt;
    color: #fdfdfd;
    padding: 0 1.3em;
    font-size: 1.05em;
    border-radius: .3rem;
    transition: background .3s;
    cursor: pointer;
    &:hover{
      background: darken($color-fisrt, 10);
    }
  }
  .note{
    text-align: center;
    height: 2.3em;
    &.success{
      color: #4db6ac;
    }
    &.error{
      color: #ef5350;
    }
  }
}