.main-nav{
  height: calc(var(--header-height) - var(--top-bar-height));
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.main-menu {
  @extend %list-style;
  height: calc(var(--header-height) - var(--top-bar-height));
  display: flex;
  align-items: center;
  justify-content: flex-end;
  display: flex;
  li, a{
    display: block;
    
  }
  
  .mobile &{
    background-color: var(--body-bg);
    position: absolute;
    top: calc(var(--header-height) - var(--top-bar-height));
    left: 0;
    flex-direction: column;
    height: auto;
    width: 100%;
    border-top: 1px solid rgba(#000, .3);
    transition: all .5s;
    max-height: 0;
    overflow: hidden;
    li{
      border-bottom: 1px solid rgba(#000,.3);
      width: 100%;
    }
  }

  a{
    line-height: 3em;
    padding: 0 1.5em;
    transition: background .3s;
    font-weight: 700;
    &:hover{
      background-color: var(--color-main);
      color: var(--body-bg)
    }
  }
}
.toggleButton{
  font-size: 1.5em;
}

.toggle-menu{
  $size: 2em;
  height: $size;
  width: $size;
  .desktop &{
    display: none;
  }
}
.main-menu.show{
  max-height: 500px;
}