.bloque1{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  margin-bottom: 4em;
  margin-top: 4em;
  .mobile & {
    display: block;
    margin-top: 2em;
  }
  img{
    width: 100% !important;
  }
  &__text{
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    font-size: 1.3em;
    
  }
  
  .buttons__container{
    display: flex;
    justify-content: center;
    margin-top: 1.5em;
    a{
      width: 25%;
      height: 2em;
      background-color: var(--color-main);
      margin-right: 1em;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: .5rem;
      &:first-child{
        img {
          width: 1.25em !important;
        }
      }
      &:last-child{
        --color-main: var(--color-first-alt);
        margin-right: 0;
      }
      img{
        width: 1.5em !important;
        height: auto;
      }
    }
  }
}
.servicios{
  text-align: center;
  margin-bottom: 4em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2em;
    .mobile &{
      grid-template-columns: repeat(2, 1fr);
      grid-gap: 1em;
    }
  }
  &__item{
    box-shadow: 0 0 10px rgba(#000,.5);
    border-radius: 1em;
    padding: 1em;
    margin-top: 2em;
    transition: transform .5s;
    transform-origin: center;
    &:hover{
      transform: scale(1.08);

    }
    img{
      width: auto !important;
      height: 150px;
      margin: 2em auto 1.5em;
      overflow: hidden;
    }
    figcaption{
      font-size: 1.3em;
      font-weight: 700;
      text-align: center;
      color: var(--color-main)
    }
  }
  > h2 {
    .mobile &{
      font-size: 1.3em !important;
    }
  }
}
h1, h2 {
  text-align: center;
  text-transform: uppercase;
  font-size: 1.8rem !important;
  .mobile & {
    font-size: 1em !important;
    margin-top: 1em;
  }
}
.video{
  $color-db-alt: darken($color-fisrt,20);
  $bd-size: 15px;
  max-width: 800px;
  margin: 4em auto 3em;
  border-radius: .1em;
  overflow: hidden;
  border: $bd-size solid darken($color-fisrt, 25);
  border-left-color: $color-db-alt;
  border-bottom-color: $color-db-alt;
}

.bloque2{
  margin-bottom: 4em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2,1fr);
    height: 500px;
    max-height: 500px;
    // overflow: hidden;
    .mobile & {
      grid-template-columns: 1fr;
      grid-template-rows: 400px repeat(2,200px);
      grid-gap: 1em;
      max-height: unset;
      height: auto;
    }
    p{
      position: absolute;
      width: 100%;
      height: auto;
      padding: 1em 2em;
      background-color: rgba(#000,.75);
      color: #fff;
      top: 0;
      left: 0;
      text-align: center;
      font-size: 1.05em;
      line-height: 1.2;
      &.consul{
        font-size: 1.3em;
        top: unset;
        bottom: 0;
      }
    }
  }
  &__item{
    position: relative;
  }
  .item1{
    grid-column: span 2;
    grid-row: span 2;
    .mobile & {
      grid-column:unset;
      grid-row:unset;
    }
  }
  .item2{
    background: cadetblue;
    &:last-child{
      background-color: darkgrey;
    }
  }
  .img-container{
    width: 100% !important;
    height: 100% !important;
    img{
      width: 100% !important;
      height: 100% !important;
      object-fit: cover;
      object-position: top center;
    }
  }
  >h2 {
    .mobile & {
      font-size: 1.3em !important;
    }
  }
}
.bloque3{
  margin-bottom: 6em;
  .mobile & {
    margin-bottom: 2em;
  }
  >h2 {
    .mobile & {
      font-size: 1.3em !important;
    }
  }
}

.testimonios-slider{
  --slider-height: 3em;
  position: relative;
  height: 250px;
  margin-top: 6em;
  .mobile & {
    margin-top: 2em;
    content: unset;
    height: 140px;
  }
}
.testimonio{
  width: 80%;
  margin: 0 auto;
  height: auto;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  position: absolute;
  font-size: 2em;
  transition: opacity .5s;
  opacity: 0;
  &.active{
    opacity: 1;
  }
  p{
    margin-bottom: 0;
    line-height: 1.1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: var(--slider-height);
    .mobile & {
      width: 100%;
      margin-left: auto;
      margin-right: auto;
    }
  }
  .q{
    position: relative;
    font-size: 1em;
    .mobile & {
      font-size: 1.3rem;
    }
    &::after{
      content: '';
      position: absolute;
      display: block;
      width: 30%;
      height: 1px;
      background-color: rgba(#000,.3);
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      .mobile & {
        content: unset;
      }
      
    }
    &::before {
      $size: 2.5rem;
      content: "\201C";
      // content: "";
      font-family: Georgia, serif;
      position: absolute;
      display: block;
      width: $size;
      height: $size;
      top: 0;
      left: -1rem;
      font-size: $size*3;
      opacity: .75;
      .mobile & {
        content: unset;
      }
    }
  }
  .n{
    font-size: .9em;
    display: flex;
    justify-content: center;
    align-items: center;
    font-style: italic;
    .mobile & {
      font-size: 1rem;
    }
    &::before{
      content: '';
      width: .5em;
      height: 1px;
      background-color: $color-text;
      position: relative;
      margin-right: .2em;
    }
  }
}
.btn-slider{
  position: absolute;
  width: 3em;
  height: 6em;
  left: 0;
  top: 50%;
  transform-origin: center;
  transform: translateY(-50%);
  cursor: pointer;
  background-color: transparent;
  border: none;
  .mobile & {
    width: 2em;
    height: 4em;
  }
  &.btn-right{
    left: unset;
    right: 0;
    transform: translateY(-50%) rotate(180deg);
  }
}
.nosotros__title{
  .mobile &{
    font-size: 1.25em !important;
  }
}